.app-header {
  height: 50px;
}
.app-header ul {
  position: fixed;
  width: 100%;
  color: white;
  background-color: black;
  line-height: 30px;
  padding: 0 15px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
  top: 0;
  -ms-flex-align: center;
  align-items: center;
}
.app-header ul li {
  margin: 10px 0;
}
.app-header ul li a {
  color: wheat;
}
.app-header ul li.more {
  width: 18px;
  height: 18px;
  background: url("./more.jpg") -10px -7px;
}



