.get {
  background-color: aliceblue;
  height: 100vh;
}
.get .nav {
  position: fixed;
  width: 100%;
  color: white;
  background-color: black;
  line-height: 30px;
  padding: 0 15px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.get .nav ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.get .nav ul li {
  padding: 10px 0;
}
.get .nav ul li a {
  color: wheat;
}
.information {
  margin: 0 auto;
}
.information li:nth-child(n + 2) {
  margin-top: 10px;
}
.information li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: start;
  justify-content: start;
}
.information li .photo {
  height: 30px;
  width: 30px;
  display: block;
}
.information li .content {
  margin-left: 10px;
}
.information li .content .name {
  display: inline;
  font-size: 12px;
  color: gray;
}
.red-bag {
  width: 200px;
}
.red-bag .instruction {
  display: inline;
}
.red-bag .bag {
  background-color: yellow;
  border-radius: 5px 5px 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: start;
  justify-content: start;
  padding-top: 10px;
  padding-bottom: 10px;
}
.red-bag .bag .ico {
  height: 35px;
  width: 30px;
  background-color: red;
  display: block;
  margin: 0 10px 0 10px;
  border-radius: 5px;
}
.red-bag .bag .text {
  display: inline;
}



