.message {
  background-color: #eeeeec;
  padding-top: 7px;
  overflow-y: auto;
}
.message .content {
  padding: 0 10px;
  margin-left: 0;
  background-color: #ffffff;
}
.message .content ul li {
  height: 40px;
  padding: 5px 0;
  border-bottom: 1px solid #d2d3d2;
}
.message .content ul li a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.message .content ul li a .left {
  display: -ms-flexbox;
  display: flex;
}
.message .content ul li a .left img {
  width: 40px;
  height: 40px;
}
.message .content ul li a .left div {
  float: right;
  margin-left: 20px;
}
.message .content ul li a .left div p:first-child {
  font-size: 14px;
}
.message .content ul li a .left div p:last-child {
  color: gray;
  font-size: 10px;
}
.message .content ul li a .right {
  color: gray;
}



