.me {
  background-color: #eeeeec;
  padding-top: 7px;
  height: calc(100vh - 100px);
  overflow-y: auto;
}
.me .information {
  background-color: #ffffff;
  padding: 5px 15px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.me .information a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  padding: 5px 0;
}
.me .information a .left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.me .information a .left img {
  width: 40px;
  height: 40px;
}
.me .information a .left div {
  margin-left: 20px;
}
.me ul {
  margin-top: 15px;
}
.me ul li:first-child {
  border: none;
  margin-bottom: 15px;
}
.me ul li {
  padding: 5px 15px;
  background-color: #ffffff;
  border-bottom: 1px solid #d2d3d2;
}
.me ul li a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-align: center;
  align-items: center;
}
.me ul li img {
  width: 20px;
  height: 20px;
}
.me ul li p {
  margin-left: 10px;
}
.me li:nth-last-child(2) {
  border: none;
}
.me li:last-child {
  border: none;
  margin-top: 15px;
}



